<template>
    <div class="qr-code">
        <dzs-header title="" v-if="!newIndex"></dzs-header>
        <div class="page">
            <h2></h2>
            <div class="item">
                <dzs-form :options="formOptions" @onSubmit="onSubmit"></dzs-form>
                <dzs-qr-code :option="options"></dzs-qr-code>
            </div>
            <h2></h2>
            <div class="item">
                <dzs-code :value="code1"></dzs-code>
                <dzs-qr-code title="" :option="options1"></dzs-qr-code>
            </div>
            <h2></h2>
            <div class="item">
                <dzs-code :value="code2"></dzs-code>
                <dzs-qr-code title="" :option="options2"></dzs-qr-code>
            </div>
            <h2>logo</h2>
            <div class="item">
                <dzs-code :value="code3"></dzs-code>
                <dzs-qr-code title="" :option="options3"></dzs-qr-code>
            </div>
            <h2></h2>
            <div class="item">
                <dzs-code :value="code4"></dzs-code>
                <dzs-qr-code title="" :option="options"></dzs-qr-code>
            </div>
            <h2></h2>
            <div class="item">
                <a target="_blank" href="https://github.com/zsduan/zsduan-summary/blob/master/src/components/dzs-qr-code/index.vue"></a>
            </div>
        </div>
        <el-backtop :visibility-height="20" v-if="!newIndex">
        </el-backtop>
    </div>
</template>
<script>
import dzsQrCode from "@/components/dzs-qr-code/index.vue";
import dzsForm from "@/components/dzs-form/index.vue";
import { code1 , code2 , code3 , code4 } from "./config";
export default {
    props:{
        newIndex : {
            type : Boolean,
            default:()=>{
                return false
            }
        }
    },
    components:{
        dzsQrCode,
        dzsForm
    },
    computed:{
        formOptions(){
            return {
                formProps : {
                    "label-width" : "120px",
                },
                formItem : [
                    {
                        label: "", 
                        key: "value", 
                        type : "input",
                        rules:[
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        props:{
                            placeholder : "",
                        },
                        defaultValue : "https://www.baidu.com",
                        span : 24
                    },
                    {
                        label: "", 
                        key: "size", 
                        type : "input",
                        rules:[
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        props:{
                            placeholder : "",
                            type : "number",
                        },
                        defaultValue : 400,
                        span : 12
                    },
                    {
                        label: "", 
                        key: "margin", 
                        type : "input",
                        rules:[
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        props:{
                            placeholder : "",
                            type : "number",
                        },
                        defaultValue : 4,
                        span : 12
                    },
                    {
                        label: "", 
                        key: "fgColor", 
                        type : "color",
                        rules:[
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        defaultValue : "#000000",
                        span : 12
                    },
                    {
                        label: "", 
                        key: "bgColor", 
                        type : "color",
                        rules:[
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        defaultValue : "#ffffff",
                        span : 12
                    },
                    {
                        label: "logo ", 
                        key: "logo", 
                        type : "input",
                        props:{
                            placeholder : "logo",
                            tips : ""
                        },
                        defaultValue : "https://yy.yunweicn.com/mp-wx-img/userIcon.png",
                        span : 24
                    },
                    {
                        label: "logo", 
                        key: "logoWidth", 
                        type : "input",
                        props:{
                            placeholder : "logo",
                            type : "number",
                        },
                        defaultValue : 50,
                        span : 12
                    },
                    {
                        label: "logo", 
                        key: "logoHeight", 
                        type : "input",
                        props:{
                            placeholder : "logo",
                            type : "number",
                        },
                        defaultValue : 50,
                        span : 12
                    },
                    {
                        label: "", 
                        key: "text", 
                        type : "input",
                        props:{
                            placeholder : "",
                        },
                        defaultValue : "zs.duan",
                        span : 24
                    },
                    {
                        label: "", 
                        key: "font", 
                        type : "input",
                        props:{
                            placeholder : "",
                        },
                        defaultValue : "bold 16px Arial",
                        span : 12
                    },
                    {
                        label: "", 
                        key: "fontColor", 
                        type : "color",
                        props:{
                            placeholder : "",
                        },
                        defaultValue : "#000000",
                        span : 12
                    },
                    {
                        label: "", 
                        key: "showBoder", 
                        type : "switch",
                        defaultValue : true,
                        span : 24
                    },
                    {
                        label: "", 
                        key: "lineWidth", 
                        type : "input",
                        props:{
                            placeholder : "",
                            type : "number",
                        },
                        defaultValue : 2,
                        span : 12
                    },
                    {
                        label: "", 
                        key: "lineColor", 
                        type : "color",
                        defaultValue : "#000000",
                        span : 12
                    },
                ]
            }
        }
    },
    data(){
        return {
            code1 : code1,
            code2 : code2,
            code3 : code3,
            code4 : code4,
            options : {
                value : "https://www.baidu.com",
                size : 400,
                margin : 4,
                fgColor : "#000000",
                bgColor : "#ffffff",
                correctLevel : "H",
                logo : "https://yy.yunweicn.com/mp-wx-img/userIcon.png",
                logoWidth : 50,
                logoHeight : 50,
                text : "zs.duan",
                font: "bold 16px Arial",
                fontColor: "#000000",
                lineWidth: 2,
                lineColor: "#000000",
                showBoder: true,
            },
            options1 : {
                value : "https://www.baidu.com",
            },
            options2 : {
                value : "https://www.baidu.com",
                size : 300,
            },
            options3 : {
                value : "https://www.baidu.com",
                logo : "https://yy.yunweicn.com/mp-wx-img/userIcon.png",
            }
        }
    },
    methods:{
        onSubmit(data){
            this.options.size = data.size;
            this.options.value = data.value;
            this.options.margin = data.margin;
            this.options.fgColor = data.fgColor;
            this.options.bgColor = data.bgColor;
            this.options.logo = data.logo;
            this.options.logoWidth = data.logoWidth;
            this.options.logoHeight = data.logoHeight;
            this.options.text = data.text;
            this.options.font = data.font;
            this.options.fontColor = data.fontColor;
            this.options.showBoder = data.showBoder;
            this.options.lineWidth = data.lineWidth;
            this.options.lineColor = data.lineColor;
            // this.options = data;
        }
    }
}
</script>
<style lang="less" scoped>
</style>